.productItemContainer {
    display: flex;
    flex-direction: column;
}

.imageContainer {
    flex: 3;
}

.productImage {
    width: 100%;
    height: 100%;
    border-radius: 10px;
    border: 1px solid var(--rose-300);
}

.addCartButton {
    width: 60%;
    background-color: white;
    padding: 10px 0;
    position: absolute;
    top: -36%;
    left: 20%;
    border-radius: 20px;
    border: 1px solid var(--rose-300);
    display: flex;
    gap: 5px;
    justify-content: center;
    cursor: pointer;
}

.addCartButton:hover {
    border-color: var(--red);
    color: var(--red);
}

.cartIcon {
    color: var(--red);
    margin-top: 3px;
}

.addCartButton span {
    font-weight: 500;
}

.productDetails {
    position: relative;
    flex: 1;
}

.productCategory {
    color: var(--rose-400);
    font-size: 14px;
    font-weight: 500;
    margin-top: 25px;
}

.productName {
    font-weight: 700;
}

.productPrice {
    color: var(--red);
    font-weight: 700;
}

.cartItemButtonContainer {
    width: 60%;
    background-color: var(--red);
    padding: 10px 15px;
    position: absolute;
    top: -36%;
    left: 20%;
    border-radius: 20px;
    border: 1px solid var(--red);
    display: flex;
    justify-content: space-between;
    align-items: center;
}

.circle {
    background-color: var(--red);
    border: 1px solid var(--rose-100);
    color: var(--rose-100);
    width: 20px;
    height: 20px;
    border-radius: 50%;
    display: flex;
    justify-content: center;
    align-items: center;
    cursor: pointer;
}

.circle:hover {
    background-color: var(--rose-100);
    border-color: var(--red);
    color: var(--red);
}

.quantity {
    color: var(--rose-100);
}